import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'
import VueAxios from 'vue-axios'
Vue.use(VueAxios, axios)

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    colorSet: {
      bgColor1: "#D1DDDB",
      bgColor2: "#85B8CB",
      bgColor3: "#1D6A96",
      bgColor4: "#283B42"
    },
    verMsg: '2023.10.4.1',
    databaseName: "herfa",  //登入廠商代號及讀取的資料庫名
    logined: false,  //登入情況->初設值為false
    loading: false,  //讀取介面顯示用,介面放在App.vue裡
    base_url: 'http://www.pddtvgame.com/herfaapi',  //線上專用指定去讀圖片或其他東西的位置
    // base_url: 'http://127.0.0.1/herfaapi',  //線上專用指定去讀圖片或其他東西的位置

    //暫存用資料
    cData: {},  //廠商資料
    pData: {},  //使用者
    customerItems: [], //客戶資料暫存
    rawmaterialItems: [], //品名資料暫存
    foldItems: [], //折工資料暫存
    punchlockItems: [], //沖鎖資料暫存
    machiningItems: [], //加工資料暫存
    materialItems:[], //物料暫存
    thirdpartyItems:[], //協力廠商暫存
    pettycashItems:[], //零用金廠商暫存
    paymentItems:null, //應收帳款暫存

    //功能名稱,樣式,key值,引用圖片,授權,icon圖片設定,
    authKeys: [
      {
        label: "工單", class: "ma-1", color: "primary", keyName: 'workdesign',
        image: require("@/assets/img/job_3d_cad_designer.png"),
        route: "/main/workdesignlist",
        authKey: "workdesign_key",
        icon: 'mdi-resize',
      },
      {
        label: "出貨單", class: "ma-1", color: "primary", keyName: 'shipment',
        image: require("@/assets/img/car_kei_truck_horo.png"),
        route: "/main/shipmentlist",
        authKey: "shipment_key",
        icon: 'mdi-post',
      },
      {
        label: "出貨單紀錄查詢", class: "ma-1", color: "primary", keyName: 'shipmentRecord',
        image: require("@/assets/img/tosyokan_book_kensaku_woman.png"),
        route: "/main/shipmentsearch",
        authKey: "shipmentRecord_key",
        icon: 'mdi-clipboard-search',
      },
      {
        label: "品名管理", class: "ma-1", color: "success", keyName: 'rawmaterial',
        image: require("@/assets/img/dougu_board_teppan.png"),
        route: "/main/rawmateriallist",
        authKey: "rawmaterial_key",
        icon: 'mdi-codepen',
      },
      {
        label: "沖鎖管理", class: "ma-1", color: "success", keyName: 'punchlock',
        image: require("@/assets/img/key_cylinder.png"),
        route: "/main/punchlocklist",
        authKey: "punchlock_key",
        icon: 'mdi-lock',
      },
      {
        label: "折工管理", class: "ma-1", color: "success", keyName: 'fold',
        image: require("@/assets/img/machine_bankin_kakou_man.png"),
        route: "/main/foldlist",
        authKey: "fold_key",
        icon: 'mdi-shape-polygon-plus',
      },
      {
        label: "加工管理", class: "ma-1", color: "success", keyName: 'machining',
        image: require("@/assets/img/machine_robot_arm.png"),
        route: "/main/machininglist",
        authKey: "machining_key",
        icon: 'mdi-plus-network',
      },
      {
        label: "材料進出管理", class: "ma-1", color: "error", keyName: 'material',
        image: require("@/assets/img/nimotsu_picking_barcode_woman.png"),
        route: "/main/materialmain",
        authKey: "material_key",
        icon: 'mdi-material-design',
      },
      {
        label: "零用金管理", class: "ma-1", color: "error", keyName: 'pettycash',
        image: require("@/assets/img/saifu_gamaguchi.png"),
        route: "/main/pettycashlist",
        authKey: "pettycash_key",
        icon: 'mdi-material-design',
      },
      {
        label: "會計帳管理", class: "ma-1", color: "error", keyName: 'accounting',
        image: require("@/assets/img/money_choubo.png"),
        route: "/main/accountsreceivablestatistics",
        authKey: "accounting_key",
        icon: 'mdi-equalizer-outline',
      },
      {
        label: "營運分析", class: "ma-1", color: "error", keyName: 'accounting',
        image: require("@/assets/img/document_data_bunseki.png"),
        route: "/main/analyize",
        authKey: "accounting_key",
        icon: 'mdi-equalizer-outline',
      },
      {
        label: "協力廠商管理", class: "ma-1", color: "warning", keyName: 'thirdparty',
        image: require("@/assets/img/company_roudou_kumiai.png"),
        route: "/main/thirdpartylist",
        authKey: "thirdparty_key",
        icon: 'mdi-handshake',
      },
      {
        label: "客戶管理", class: "ma-1", color: "warning", keyName: 'customer',
        image: require("@/assets/img/building_kaisya.png"),
        route: "/main/customerlist",
        authKey: "customer_key",
        icon: 'mdi-office-building',
      },
      {
        label: "人員管理", class: "ma-1", color: "purple white--text", keyName: 'personnel',
        image: require("@/assets/img/kojinjouhou_businessman.png"),
        route: "/main/personnellist",
        authKey: "personnel_key",
        icon: 'mdi-card-account-details',
      },
      {
        label: "打卡管理", class: "ma-1", color: "purple white--text", keyName: 'timecard',
        image: require("@/assets/img/timecard_machine.png"),
        route: "/main/timecardlist",
        authKey: "timecard_key",
        icon: 'mdi-clock-time-three',
      },

      {
        label: "登出", class: "ma-1 white--text", color: "dark", keyName: 'exit',
        image: require("@/assets/img/text_exit.png"),
        route: "/login",
        authKey: "exit_key",
        icon: 'mdi-logout',
      },
    ],
  },
  mutations: {
    logined(state, val) {  //切換登入
      state.logined = val
    },
    setLoading(state, val) {  //設定讀取畫面On
      state.loading = val
    },
    setcData(state, val) {  //設定廠商資料
      state.cData = val
    },
    setpData(state, val) {  //設定登入者資料
      state.pData = val
    },
    setDatabaseName(state, val) {  //設定使用的資料庫
      state.databaseName = val;
    },
    setData(state,val){
      state[val.tableName+'Items'] = val.data;
    },
  },
  actions: {
    getItems(context,tableName){ //取得XX全部資料
      axios.post(
        "general/getAll/" + this.state.databaseName + "/" + tableName
      ).then((rs) => {
        let newData = rs.data.map((i) => {
          let newItem = JSON.parse(i.datalist);
          newItem.snkey = i.snkey;
          if (newItem.picname == undefined) {
            newItem.picname = "lazypic.jpg";
          }
          return newItem;
        });

        newData = newData.sort((a, b) => {
          return a.name > b.name ? 1 : -1;
        });

        let postData ={
          tableName: tableName,
          data:newData
        }

        context.commit('setData', postData);
      });
    },
    
  },
  modules: {
  }
})
