import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

const routes = [
  { path: '*', redirect: '/' },
  { path: '/', redirect: '/login' },
  { path: '/login', name: 'login', component: resolve => require(['@/views/login/Login.vue'], resolve), meta: { title: 'Login' } },
  {
    path: '/main', name: 'main', component: resolve => require(['@/views/main/Main.vue'], resolve),
    children: [
      { path: 'functionlist', name: 'functionlist', component: resolve => require(['@/views/main/FunctionList.vue'], resolve) },

      { path: 'materialmain', name: 'materialmain', component: resolve => require(['@/views/main/material/MaterialMain.vue'], resolve) },
      { path: 'workdesignlist', name: 'workdesignlist', component: resolve => require(['@/views/main/WorkdesignList.vue'], resolve) },
      { path: 'shipmentlist', name: 'shipmentlist', component: resolve => require(['@/views/main/ShipmentList.vue'], resolve) },
      { path: 'shipmentsearch', name: 'shipmentsearch', component: resolve => require(['@/views/main/ShipmentSearch.vue'], resolve) },
      { path: 'rawmateriallist', name: 'rawmateriallist', component: resolve => require(['@/views/main/RawmaterialList.vue'], resolve) },
      { path: 'foldlist', name: 'foldlist', component: resolve => require(['@/views/main/FoldList.vue'], resolve) },
      { path: 'punchlocklist', name: 'punchlocklist', component: resolve => require(['@/views/main/PunchlockList.vue'], resolve) },
      { path: 'machininglist', name: 'machininglist', component: resolve => require(['@/views/main/MachiningList.vue'], resolve) },
      { path: 'customerlist', name: 'customerlist', component: resolve => require(['@/views/main/CustomerList.vue'], resolve) },
      { path: 'thirdpartylist', name: 'thirdpartylist', component: resolve => require(['@/views/main/ThirdpartyList.vue'], resolve) },
      { path: 'personnellist', name: 'personnellist', component: resolve => require(['@/views/main/PersonnelList.vue'], resolve) },
      { path: 'timecardlist', name: 'timecardlist', component: resolve => require(['@/views/main/timecard/TimecardList.vue'], resolve) },
      { path: 'pettycashlist', name: 'pettycashlist', component: resolve => require(['@/views/main/PettycashList.vue'], resolve) },
      { path: 'accountsreceivablestatistics', name: 'accountsreceivablestatistics', component: resolve => require(['@/views/main/accounting/AccountsReceivableStatistics.vue'], resolve) },
      { path: 'analyize', name: 'analyize', component: resolve => require(['@/views/main/analyize'], resolve) },

    ]
  },
  {
    path: '/print', name: 'print', component: resolve => require(['@/views/print/Main.vue'], resolve),
    children: [
      { path: 'workdesignprint', name: 'workdesignprint', component: resolve => require(['@/views/print/WorkdesignPrint.vue'], resolve) },
      { path: 'shipmentprint', name: 'shipmentprint', component: resolve => require(['@/views/print/ShipmentPrint.vue'], resolve) },
      { path: 'shipmentlistprint', name: 'shipmentlistprint', component: resolve => require(['@/views/print/ShipmentListPrint.vue'], resolve) },
      { path: 'customershipmentlistprint', name: 'customershipmentlistprint', component: resolve => require(['@/views/print/CustomerShipmentListPrint.vue'], resolve) },

    ]
  },
  { path: '/timeclockon', name: 'timeclockon', component: resolve => require(['@/views/main/timecard/TimeClockOn.vue'], resolve) },
]

const router = new VueRouter({
  // mode: 'history',
  base: process.env.BASE_URL,
  routes
})

router.beforeEach((to, from, next) => {
  if (to.meta.title != undefined) {
    window.document.title = to.meta.title;
  } else {
    window.document.title = '賀發不銹鋼實業有限公司';
  }
  next()
})

export default router
