import Vue from 'vue'
import App from './App.vue'
import './registerServiceWorker'
import router from './router'
import store from './store'
import vuetify from './plugins/vuetify';
import "@/assets/css/mytransition.css"  //自己設定的各項過場動畫
import "@/assets/css/mystyle.css"  //自己設定的css樣式

import api from "@/assets/js/api.js";
Vue.prototype.$api = api

// axios 設定
import axios from 'axios'
import VueAxios from 'vue-axios'
import qs from "qs"

Vue.use(VueAxios, axios)
axios.defaults.baseURL = 'http://www.pddtvgame.com/herfaapi'  //baseurl(伺服器時用)
// axios.defaults.baseURL = 'http://127.0.0.1/herfaapi'  //baseurl(單機專用)

Vue.prototype.myAxios = function (url = null, postData = null) {//myAxios是函式名
  this.$store.commit("setLoading", true); //讀取畫面on
  return new Promise((resolve, reject) => {
    axios
      .post(url, qs.stringify(postData))
      .then((rs) => {
        this.$store.commit("setLoading", false); //讀取畫面off
        resolve(rs.data);
      })
      .catch((err) => {
        this.$store.commit("setLoading", false); //讀取畫面off
        reject(err);
      });
  });
}
//更新庫存用
Vue.prototype.updateStore = function (document, matchData) {
  let postData = {
    snkey: matchData.snkey,
    datalist: JSON.stringify(matchData),
  };
  let url = "general/edit/" + this.$store.state.databaseName + "/" + document;
  this.myAxios(url, postData)
    .then((rs) => {
      console.log(rs);
      if (rs.state == 1) {
        // this.$swal({ icon: "success", text: "庫存已更新", });
        console.log("庫存已更新");
      }
    })
    .catch((err) => {
      this.$swal({ icon: "err", text: "資料存取錯誤!!內容:" + err });
    });
}

//vue-sweetalert2 引用及設定
import VueSweetalert2 from 'vue-sweetalert2';
// If you don't need the styles, do not connect
import 'sweetalert2/dist/sweetalert2.min.css';
//設定初始值
let options = {
  toast: true,
  position: 'bottom',
  showConfirmButton: false,
  timerProgressBar: true,
  timer: 5000,
};
Vue.use(VueSweetalert2, options);

//設定全域函數 -> 刪除時的vue-sweetalert2顯示格式
Vue.prototype.yn = function (title = '確認刪除!?') {
  return this.$swal({
    icon: "warning",
    title: title,
    toast: false,
    position: "center",
    showConfirmButton: true,
    showCancelButton: true,
    timerProgressBar: false,
    timer: 0,
    confirmButtonText: `確認`,
    cancelButtonText: `取消`,
    confirmButtonColor: "green",
    cancelButtonColor: "red",
    focusCancel: true,
  });
}

Vue.config.productionTip = false

new Vue({
  router,
  store,
  vuetify,
  render: h => h(App)
}).$mount('#app')
